.card_items {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 17px;
}

.card {
  flex: 1 0 auto;
  width: 325px;
  height: 328px;
  border-radius: 19px;
  position: relative;
  overflow: hidden;
  margin: 0;
  padding: 0;
  border: 1px solid rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
}

.card__text {
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  color: rgba(0, 0, 0, 0.6);
  background-color: #fff;
  border-radius: 4px;
  padding: 2px 12px;
  margin: 0;
  position: absolute;
  top: 16px;
  left: 50%;
  transform: translateX(-50%);
}

.card__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  margin: 0;
  padding: 0;
  cursor: zoom-in;
}
