.switch {
  display: flex;
  position: relative;
}
.switch__box {
  border-radius: 25px;
  border: 2px solid black;
  width: 50px;
  height: 30px;
  /* outline: none; */
  appearance: none;
}
.switch__slider {
  border-radius: 20px;
  width: 30px;
  height: 30px;
  background-color: black;
  color: white;
  text-align: center;
  cursor: pointer;
  position: absolute;
}
.switch__slider-F {
  top: 3px;
  left: 23px;
}
.switch__slider-C {
  top: 2px;
  left: 5px;
}
.switch__active {
  color: white;
  font-size: 16px;
}
.switch__temp-F {
  position: absolute;
  top: -7px;
  left: 38px;
  color: white;
}
.switch__temp-C {
  position: absolute;
  top: -6px;
  left: 12px;
  color: white;
}
